/* Primary button css */

.primary-button {
    padding: 15px 25px;
    background-color: #4d92fb;
    color: #fff;
    font-size: 16px;
    font-family: helvetica neue;
    border: 1px solid #00000033;
    border-radius: 8px;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.5s ease;
}
.primary-button:focus {
    outline: none;
}

.primary-button:hover {
    background-color: #196be5;
    transition: background-color 0.5s ease;
}

.primary-button-disabled {
    padding: 15px 25px;
    background-color: #dae3ef;
    color: #8f99a7;
    font-size: 16px;
    font-family: helvetica neue;
    border: 1px solid #d2d9e2;
    border-radius: 8px;
    font-weight: 500;
}
.primary-button-disabled:focus {
    outline: none;
}

.primary-button-ghost {
    padding: 15px 25px;
    background-color: #fff;
    color: #196be5;
    font-size: 16px;
    font-family: helvetica neue;
    border: 2px solid #196be5;
    border-radius: 8px;
    font-weight: 600;
    cursor: pointer;
}
.primary-button-ghost:hover {
    background-color: #4d92fb;
    transition: background-color 0.5s ease;
    color: #fff;
    font-weight: 600;
}
.primary-button-ghost:focus {
    outline: none;
}

.primary-button-ghost-disabled {
    padding: 15px 25px;
    background-color: #fff;
    color: #8f99a7;
    font-size: 16px;
    font-family: helvetica neue;
    border: 2px solid #8f99a7;
    border-radius: 8px;
    font-weight: 500;
}

.primary-button-ghost-disabled:focus {
    outline: none;
}

/* color variations primary emerald-green*/
button.primary-button.emerald-green {
    background-color: #4dd583;
}
button.primary-button.emerald-green:hover {
    background-color: #27c466;
}
button.primary-button-ghost.emerald-green {
    color: #27c466;
    border-color: #27c466;
}
button.primary-button-ghost.emerald-green:hover {
    color: #fff;
    background-color: #4dd583;
    border-color: #00000033;
}

/* color variations primary peachish*/
button.primary-button.peachish {
    background-color: #fb7469;
}
button.primary-button.peachish:hover {
    background-color: #f04f43;
}
button.primary-button-ghost.peachish {
    color: #fb7469;
    border-color: #fb7469;
}
button.primary-button-ghost.peachish:hover {
    color: #fff;
    background-color: #fb7469;
    border-color: #00000033;
}

